<template>

    <el-card class="register-card" shadow="never" v-loading=this.$store.state.Register.loading>
        <el-form :model="registerForm" ref="registerForm" :status-icon="true" :rules="rules" class="register-container"
                 label-width="100px" label-position="left">
            <h3></h3>
            <el-form-item label="" prop="username" :rules="[
                            { required: true, message: ''}]">
                <el-input type="text" v-model="registerForm.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="password" :rules="[
                            { required: true, message: ''}]">
                <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="checkPass">
                <el-input type="password" v-model="registerForm.checkPass" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="phonenum" :rules="[
                            { required: true, message: ''},
                            {type:'number', message: ''}]">
                <el-input type="text" v-model.number="registerForm.phonenum" placeholder=""></el-input>
            </el-form-item>
            <el-button v-on:click="register"></el-button>
        </el-form>
        <div class="some-bt">
            <el-button type="text" v-on:click="jumpLogin"></el-button>
        </div>
    </el-card>

</template>

<script>


    export default {
        name: "Register",
        data() {
            return {
                registerForm: {
                    username: '',
                    password: '',
                    checkPass: '',
                    phonenum: ''
                },
                rules: {
                    checkPass: [
                        {validator: this.$store.state.Register.checkTwoPass, trigger: 'blur', required: true}
                    ],
                },
                responseResult: []
            }
        },
        methods: {
            register() {
                this.$refs['registerForm'].validate((valid) => {
                    if (valid) {
                        this.$store.dispatch('Register/register', this.registerForm)
                            .then(
                                (value) => {
                                    this.$notify({
                                        title: value,
                                        type: 'success',
                                        position: 'top-left',
                                    })
                                    this.$router.push({name: 'Login'})
                                },
                                (err) => {
                                    this.$notify({
                                        title: err,
                                        type: 'error',
                                        position: 'top-left',
                                    })
                                }
                            )
                    }
                });

            },
            jumpLogin() {
                this.$router.push({path: '/login'});
                console.log("")
            }
        }
    }
</script>

<style scoped>
    .register-card {
        width: 350px;
        position: center;
        margin: 10% auto auto auto;
        box-shadow: 0 0 10px #cac6c6;
    }

    .register-card:hover {
        box-shadow: 0 0 25px #cac6c6;
    }

    .some-bt {
        alignment: left;
        text-align: right;
        margin-top: 5%;
        margin-right: 10%;
    }
</style>